html {
  font-size: 16px;
}
body {
  font-family: 'Roboto', sans-serif;
}
@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}
.block {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
}
h2 {
  font-weight: 400;
  margin-bottom: 30px;
  font-size: 1.625rem;
  line-height: normal;
}
